}
.fieldset-radio__label {
  display: flex;
  align-items: center;
}
.fieldset-radio__mark {
  position: relative;
  display: block;
  width: 24px;
  height: 24px;
  margin-right: 10px;
  border-radius: 50%;
  border: 2px solid #bdbdbd;
}
.fieldset-radio__input:checked + .fieldset-radio__mark::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  background-color: #7859cf;
  border-radius: 50%;
}
.fieldset-radio__input:checked + .fieldset-radio__mark {
  border: 2px solid #7859cf;
}
