}
.navigation__toggle {
  position: absolute;
  right: 22px;
  top: 24px;
  width: 16px;
  height: 12px;
  padding: 2px 0;
  background-color: transparent;
  border: 0;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation--closed .navigation__toggle-bar {
  display: block;
  height: 2px;
  width: 16px;
  background-color: #333333;
}
.navigation--closed .navigation__toggle-bar::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 16px;
  height: 2px;
  background-color: #333333;
}
.navigation--closed .navigation__toggle-bar::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 16px;
  height: 2px;
  background-color: #333333;
}
@media (max-width: 767px) {
  .navigation--closed .navigation__list {
    display: none;
  }
}
.navigation__user-menu {
  display: flex;
  flex-wrap: wrap;
  margin: 0 38px 0 0;
  padding: 0;
}
@media (min-width: 768px) {
  .navigation__user-menu {
    min-width: 120px;
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .navigation__user-menu {
    min-width: 258px;
    margin-right: -30px;
  }
}
.navigation--opened .navigation__toggle-bar::before,
.navigation--opened .navigation__toggle-bar::after {
  content: "";
  position: absolute;
  top: 6px;
  left: 0;
  width: 16px;
  height: 2px;
  background-color: #333333;
}
.navigation--opened .navigation__toggle-bar::before {
  transform: rotate(45deg);
}
.navigation--opened .navigation__toggle-bar::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .navigation--closed .navigation__list {
    display: none;
  }
}
.navigation--nojs .navigation__toggle {
  display: none;
}
@media (max-width: 767px) {
  .navigation--nojs .navigation__list {
    position: static;
    width: 320px;
    margin: 0 0 0 -140px;
    order: 1;
  }
