.page {
  height: 100%;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
}

.page__header {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  @media (min-width: @desktop-width) {
    width: 1360px;
    margin: 0 auto;
  }
}

.page__main-footer {
  @media (min-width: @desktop-width) {
    width: 1360px;
    margin: 0 auto;
  }
}
